*,
*::before,
*::after {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

:root {
    --body-backcolor: rgba(48, 18, 18, 0.5);
    --main-container-top-backcolor: rgb(59, 21, 21);
    --main-container-bottom-backcolor: rgb(58, 21, 21);
    
    --navigation-item-backcolor: rgb(245, 245, 220);
    --navigation-item-backcolor-hover: rgba(109, 14, 233, 1);
    --navigation-item-forecolor: rgb(77, 105, 228);
    --navigation-item-forecolor-hover: rgb(255, 255, 255);
    --navigation-current-page-after-backcolor: rgb(185, 75, 185);
    
    --main-link-forecolor: rgba(86, 194, 227, 1);
    --main-link-hover-forecolor: rgba(106, 214, 247, 1);
    
    --footer-color: rgba(48, 18, 18, 1);
    
    --section-background: rgb(100, 114, 193);
    --section-boxes: rgb(96, 103, 146);
    --section-margin: rgb(43, 48, 77);
    --white: rgb(255, 255, 255);
    --black: rgb(0, 0, 0);
    --blue: rgb(0, 0, 255);
    --red: rgb(255, 0, 0);
    --yellow: rgb(255, 255, 0);
}

/* Globalne resetowanie stylow */
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul {
    margin: 0;
    padding: 0;
}

img {
    display: block;
    max-width: 100%;
    height: auto;
}

.button {
    display: flex;
    padding: 12px 32px;
    border: none;
    border-radius: 4px;
    background-color: #2196f3;
    color: #fff;
    font-size: inherit;
    cursor: pointer;
    margin: 0 auto;
}

.button:hover,
.button:focus {
    background-color: #1976d2;
}

ul,
ol {
    list-style: none;
    margin: 0;
    padding-left: 0;
}

span {
    font-weight: 600;
}

a{
    text-decoration: none;
}
.main-container a {
    color: var(--main-link-forecolor);
    transition: color 750ms cubic-bezier(0.4, 0, 0.2, 1);
}

.main-container a:hover {
    color: var(--main-link-hover-forecolor);
    
}

body {
    font-family: 'Helvetica Neue', 'Roboto', 'Verdana', 'Tahoma', sans-serif;
    background: var(--body-backcolor);
    color: rgb(45, 46, 48);
    line-height: 1.5;
    font-size: 16px;
    font-weight: 400;
    font-style: normal;
    width: 1280px;
    margin: 0 auto;
}


 
// Menu    
.menu-sticky {
    width: 100%;
    position: sticky;
    top: -1px;
    margin: 0;
    text-align: center;
    text-transform: uppercase;
    box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2), 0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
    z-index: 9000;
}

.nav-list-container {
    display: flex;
    justify-content: start;
    flex-wrap: wrap;
    gap: 0 5px;
    padding: 10px 30px 20px;
    background-color: transparent;
}

.nav-list-container a {
    position: relative;
    display: flex;
    padding: 10px;
    color: var(--navigation-item-forecolor);
    background-color: var(--navigation-item-backcolor);
    gap: 10px;
    margin: 10px auto;
    padding: 10px;
    font-size: 14px;
    box-shadow: 0px 2px 1px -1px rgba(0, 0, 0, 0.2),
        0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 1px 3px 0px rgba(0, 0, 0, 0.12);

        transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.nav-list-container a:hover {
    color: var(--navigation-item-forecolor-hover);
   
    background-color: var(--navigation-item-backcolor-hover);
}

.current-page::after {
    content: '';
    width: 100%;
    height: 4px;
    border-radius: 2px;
    background-color: var(--navigation-current-page-after-backcolor);
    position: absolute;
    bottom: -4px;
    left: 0;
}

// Main
.main-container {
    background-image: linear-gradient(to bottom, var(--main-container-top-backcolor), var(--main-container-bottom-backcolor));
}

// Footer
.footer {
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: center;
    text-align: center;
    background-color: var(--footer-color);
    padding: 15px 10px;
    color: var(--white);
    text-shadow: 2px 2px 8px var(--white);
    box-shadow: inset 0px 2px 1px -1px rgba(255, 255, 255, 0.2),
            0px 1px 1px 0px rgba(255, 255, 255, 0.14), 0px 1px 3px 0px rgba(255, 253, 253, 0.12);        
}

.footer-logo {
    display: flex;
    flex-flow: column nowrap;
    width: 32px;
    align-items: center;
}

.footer-date {
    align-self: flex-end;
    color: var(--black);
    text-shadow: none;
}